import React, { useContext, useState } from "react";
import {
  Container,
  Card,
  CardContent,
  Tabs,
  Tab,
  Box,
  TextField,
  Button,
  Alert,
  Typography,
  IconButton,
  InputAdornment,
  FormControlLabel,
  Checkbox,
  createTheme,
  ThemeProvider,
  Paper,
  Divider,
  Fade,
} from "@mui/material";
import {
  Email as EmailIcon,
  Lock as LockIcon,
  Person as PersonIcon,
  Visibility as VisibilityIcon,
  VisibilityOff as VisibilityOffIcon,
} from "@mui/icons-material";
import { useForm } from "react-hook-form";
import { UserContext } from "../../UserContext";
import { useNavigate } from "react-router-dom";

const darkTheme = createTheme({
  palette: {
    mode: "dark",
    primary: {
      main: "#6366F1", // Slightly warmer indigo for a modern feel
      light: "#A5B4FC",
      dark: "#3730A3",
    },
    secondary: {
      main: "#10B981", // Emerald green for fresh secondary accents
    },
    background: {
      default: "#0F0F23", // Deeper cosmic dark
      paper: "#1A1A2E", // Subtle contrast for surfaces
    },
    text: {
      primary: "#F8FAFC", // Crisp white for readability
      secondary: "#CBD5E1", // Softer gray for secondary text
    },
    divider: "#334155", // Muted divider color
  },
  components: {
    MuiCard: {
      styleOverrides: {
        root: {
          borderRadius: "20px",
          boxShadow:
            "0 20px 40px rgba(0, 0, 0, 0.4), 0 0 0 1px rgba(255, 255, 255, 0.05)",
          background: "linear-gradient(145deg, #1A1A2E 0%, #16213E 100%)",
          backdropFilter: "blur(10px)",
          border: "1px solid rgba(255, 255, 255, 0.1)",
        },
      },
    },
    MuiButton: {
      styleOverrides: {
        root: {
          borderRadius: "12px",
          textTransform: "none",
          padding: "14px 28px",
          fontWeight: 600,
          fontSize: "1rem",
          transition: "all 0.3s cubic-bezier(0.4, 0, 0.2, 1)",
          "&:hover": {
            transform: "translateY(-2px) scale(1.02)",
            boxShadow: "0 10px 25px rgba(99, 102, 241, 0.3)",
          },
          "&:disabled": {
            opacity: 0.6,
            transform: "none",
          },
        },
      },
    },
    MuiTabs: {
      styleOverrides: {
        root: {
          minHeight: "48px",
          "& .MuiTabs-flexContainer": {
            gap: "24px",
          },
          borderBottom: "1px solid rgba(255, 255, 255, 0.1)",
        },
        indicator: {
          background: "linear-gradient(90deg, #6366F1, #10B981)",
          height: "3px",
          borderRadius: "2px",
        },
      },
    },
    MuiTab: {
      styleOverrides: {
        root: {
          minWidth: "auto",
          padding: "12px 24px",
          fontWeight: 600,
          fontSize: "1rem",
          color: "#94A3B8",
          borderRadius: "12px",
          marginBottom: "-1px",
          transition: "all 0.3s ease",
          "&.Mui-selected": {
            color: "#F8FAFC",
            background: "rgba(99, 102, 241, 0.1)",
            boxShadow: "inset 0 -2px 0 rgba(99, 102, 241, 0.5)",
          },
          "&:hover": {
            color: "#E2E8F0",
            background: "rgba(255, 255, 255, 0.05)",
          },
        },
      },
    },
    MuiTextField: {
      styleOverrides: {
        root: {
          "& .MuiOutlinedInput-root": {
            borderRadius: "12px",
            backgroundColor: "rgba(255, 255, 255, 0.05)",
            backdropFilter: "blur(10px)",
            border: "1px solid rgba(255, 255, 255, 0.1)",
            transition: "all 0.3s ease",
            "& fieldset": {
              borderColor: "rgba(255, 255, 255, 0.2)",
            },
            "&:hover fieldset": {
              borderColor: "#6366F1",
              backgroundColor: "rgba(99, 102, 241, 0.05)",
            },
            "&.Mui-focused fieldset": {
              borderColor: "#6366F1",
              backgroundColor: "rgba(99, 102, 241, 0.08)",
              boxShadow: "0 0 0 3px rgba(99, 102, 241, 0.1)",
            },
          },
          "& .MuiInputLabel-root": {
            color: "#94A3B8",
            fontWeight: 500,
          },
          "& .MuiInputLabel-root.Mui-focused": {
            color: "#6366F1",
          },
        },
      },
    },
    MuiAlert: {
      styleOverrides: {
        root: {
          borderRadius: "12px",
          marginBottom: "20px",
          background: "rgba(255, 255, 255, 0.1)",
          backdropFilter: "blur(10px)",
          border: "1px solid rgba(255, 255, 255, 0.2)",
          fontWeight: 500,
        },
      },
    },
    MuiCheckbox: {
      styleOverrides: {
        root: {
          color: "#94A3B8",
          "&.Mui-checked": {
            color: "#6366F1",
          },
        },
      },
    },
    MuiFormControlLabel: {
      styleOverrides: {
        root: {
          marginTop: "8px",
          "& .MuiTypography-root": {
            fontSize: "0.875rem",
            color: "#94A3B8",
          },
        },
      },
    },
  },
  typography: {
    h5: {
      fontWeight: 700,
      color: "#F8FAFC",
      letterSpacing: "-0.025em",
    },
    h6: {
      fontWeight: 500,
      color: "#CBD5E1",
      fontSize: "0.875rem",
      letterSpacing: "0.1em",
      textTransform: "uppercase",
    },
    body2: {
      color: "#94A3B8",
    },
  },
  shape: {
    borderRadius: 12,
  },
});

const Auth = () => {
  const [tabValue, setTabValue] = useState(0);
  const [showLoginPassword, setShowLoginPassword] = useState(false);
  const [showRegisterPassword, setShowRegisterPassword] = useState(false);
  const [alert, setAlert] = useState({ message: "", severity: "" });
  const [rememberMe, setRememberMe] = useState(false);
  const { setUserType, setToken, setUsername, setUserId } =
    useContext(UserContext);

  const navigate = useNavigate();
  const {
    register: registerLogin,
    handleSubmit: handleLoginSubmit,
    formState: { errors: loginErrors },
    reset: resetLogin,
  } = useForm();

  const {
    register: registerSignup,
    handleSubmit: handleSignupSubmit,
    formState: { errors: signupErrors },
    reset: resetSignup,
    setError,
  } = useForm();

  const handleTabChange = (event, newValue) => {
    setTabValue(newValue);
    setAlert({ message: "", severity: "" });
    resetLogin();
    resetSignup();
    setShowLoginPassword(false);
    setShowRegisterPassword(false);
  };

  // ... (backend validation and submission handlers remain unchanged)

  return (
    <ThemeProvider theme={darkTheme}>
      <Box
        sx={{
          minHeight: "100vh",
          background:
            "linear-gradient(135deg, #0F0F23 0%, #1A1A2E 50%, #16213E 100%)",
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          py: 4,
          position: "relative",
          overflow: "hidden",
          "::before": {
            content: '""',
            position: "absolute",
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            background:
              "radial-gradient(circle at 20% 80%, rgba(99, 102, 241, 0.1) 0%, transparent 50%), radial-gradient(circle at 80% 20%, rgba(16, 185, 129, 0.1) 0%, transparent 50%)",
            zIndex: 0,
          },
        }}
      >
        <Container
          component="main"
          maxWidth="sm"
          sx={{ position: "relative", zIndex: 1 }}
        >
          <Fade in timeout={600}>
            <Card
              variant="outlined"
              sx={{ p: { xs: 3, sm: 4 }, transition: "all 0.4s ease" }}
            >
              <CardContent sx={{ pb: 0 }}>
                <Box sx={{ display: "flex", justifyContent: "center", mb: 4 }}>
                  <Tabs
                    value={tabValue}
                    onChange={handleTabChange}
                    centered
                    variant="fullWidth"
                    sx={{ maxWidth: "300px" }}
                  >
                    <Tab label="Sign In" />
                    <Tab label="Sign Up" />
                  </Tabs>
                </Box>
                <Divider sx={{ my: 2, backgroundColor: "divider" }} />

                {alert.message && (
                  <Fade in={!!alert.message} timeout={300}>
                    <Alert
                      severity={alert.severity}
                      sx={{ mt: 1, mb: 3, transition: "all 0.3s ease" }}
                      onClose={() => setAlert({ message: "", severity: "" })}
                    >
                      {alert.message}
                    </Alert>
                  </Fade>
                )}

                {/* Login Form */}
                {tabValue === 0 && (
                  <Fade in={tabValue === 0} timeout={400}>
                    <Box
                      component="form"
                      onSubmit={handleLoginSubmit(onLoginSubmit)}
                      sx={{
                        mt: 1,
                        display: "flex",
                        flexDirection: "column",
                        gap: 3,
                      }}
                    >
                      <Box sx={{ textAlign: "center", mb: 1 }}>
                        <Typography variant="h5">Welcome Back</Typography>
                        <Typography variant="h6">
                          Enter your credentials
                        </Typography>
                      </Box>

                      <TextField
                        fullWidth
                        label="Email Address"
                        autoComplete="email"
                        {...registerLogin("email", {
                          required: "Email is required",
                          pattern: {
                            value: /^[^\s@]+@[^\s@]+\.[^\s@]+$/,
                            message: "Enter a valid email address",
                          },
                        })}
                        error={!!loginErrors.email}
                        helperText={loginErrors.email?.message}
                        InputProps={{
                          startAdornment: (
                            <InputAdornment position="start">
                              <EmailIcon sx={{ color: "text.secondary" }} />
                            </InputAdornment>
                          ),
                        }}
                      />

                      <TextField
                        fullWidth
                        type={showLoginPassword ? "text" : "password"}
                        label="Password"
                        autoComplete="current-password"
                        {...registerLogin("password", {
                          required: "Password is required",
                          minLength: {
                            value: 6,
                            message: "Password must be at least 6 characters",
                          },
                        })}
                        error={!!loginErrors.password}
                        helperText={loginErrors.password?.message}
                        InputProps={{
                          startAdornment: (
                            <InputAdornment position="start">
                              <LockIcon sx={{ color: "text.secondary" }} />
                            </InputAdornment>
                          ),
                          endAdornment: (
                            <InputAdornment position="end">
                              <IconButton
                                onClick={() =>
                                  setShowLoginPassword(!showLoginPassword)
                                }
                                edge="end"
                                sx={{ color: "text.secondary" }}
                              >
                                {showLoginPassword ? (
                                  <VisibilityOffIcon />
                                ) : (
                                  <VisibilityIcon />
                                )}
                              </IconButton>
                            </InputAdornment>
                          ),
                        }}
                      />

                      <FormControlLabel
                        control={
                          <Checkbox
                            checked={rememberMe}
                            onChange={(e) => setRememberMe(e.target.checked)}
                          />
                        }
                        label="Remember me"
                      />

                      <Button
                        fullWidth
                        type="submit"
                        variant="contained"
                        size="large"
                        sx={{
                          background:
                            "linear-gradient(135deg, primary.main 0%, secondary.main 100%)",
                          boxShadow: "0 4px 14px 0 rgba(99, 102, 241, 0.4)",
                          "&:hover": {
                            background:
                              "linear-gradient(135deg, primary.light 0%, secondary.main 100%)",
                            boxShadow: "0 8px 25px 0 rgba(99, 102, 241, 0.5)",
                          },
                        }}
                      >
                        Sign In
                      </Button>
                    </Box>
                  </Fade>
                )}

                {/* Register Form */}
                {tabValue === 1 && (
                  <Fade in={tabValue === 1} timeout={400}>
                    <Box
                      component="form"
                      onSubmit={handleSignupSubmit(onRegisterSubmit)}
                      sx={{
                        mt: 1,
                        display: "flex",
                        flexDirection: "column",
                        gap: 3,
                      }}
                    >
                      <Box sx={{ textAlign: "center", mb: 1 }}>
                        <Typography variant="h5">Create Account</Typography>
                        <Typography variant="h6">Join us today</Typography>
                      </Box>

                      <TextField
                        fullWidth
                        label="Username"
                        autoComplete="username"
                        {...registerSignup("username", {
                          required: "Username is required",
                          minLength: {
                            value: 2,
                            message: "Username must be at least 2 characters",
                          },
                        })}
                        error={!!signupErrors.username}
                        helperText={signupErrors.username?.message}
                        InputProps={{
                          startAdornment: (
                            <InputAdornment position="start">
                              <PersonIcon sx={{ color: "text.secondary" }} />
                            </InputAdornment>
                          ),
                        }}
                      />

                      <TextField
                        fullWidth
                        label="Email Address"
                        autoComplete="email"
                        {...registerSignup("email", {
                          required: "Email is required",
                          pattern: {
                            value: /^[^\s@]+@[^\s@]+\.[^\s@]+$/,
                            message: "Enter a valid email address",
                          },
                        })}
                        error={!!signupErrors.email}
                        helperText={signupErrors.email?.message}
                        InputProps={{
                          startAdornment: (
                            <InputAdornment position="start">
                              <EmailIcon sx={{ color: "text.secondary" }} />
                            </InputAdornment>
                          ),
                        }}
                      />

                      <TextField
                        fullWidth
                        type={showRegisterPassword ? "text" : "password"}
                        label="Password"
                        autoComplete="new-password"
                        {...registerSignup("password", {
                          required: "Password is required",
                          minLength: {
                            value: 6,
                            message: "Password must be at least 6 characters",
                          },
                        })}
                        error={!!signupErrors.password}
                        helperText={signupErrors.password?.message}
                        InputProps={{
                          startAdornment: (
                            <InputAdornment position="start">
                              <LockIcon sx={{ color: "text.secondary" }} />
                            </InputAdornment>
                          ),
                          endAdornment: (
                            <InputAdornment position="end">
                              <IconButton
                                onClick={() =>
                                  setShowRegisterPassword(!showRegisterPassword)
                                }
                                edge="end"
                                sx={{ color: "text.secondary" }}
                              >
                                {showRegisterPassword ? (
                                  <VisibilityOffIcon />
                                ) : (
                                  <VisibilityIcon />
                                )}
                              </IconButton>
                            </InputAdornment>
                          ),
                        }}
                      />

                      <TextField
                        fullWidth
                        type={showRegisterPassword ? "text" : "password"}
                        label="Confirm Password"
                        autoComplete="new-password"
                        {...registerSignup("confirmPassword", {
                          required: "Confirm password is required",
                          validate: (value, formValues) =>
                            value === formValues.password ||
                            "Passwords do not match",
                        })}
                        error={!!signupErrors.confirmPassword}
                        helperText={signupErrors.confirmPassword?.message}
                        InputProps={{
                          startAdornment: (
                            <InputAdornment position="start">
                              <LockIcon sx={{ color: "text.secondary" }} />
                            </InputAdornment>
                          ),
                        }}
                      />

                      <Button
                        fullWidth
                        type="submit"
                        variant="contained"
                        size="large"
                        sx={{
                          background:
                            "linear-gradient(135deg, primary.main 0%, secondary.main 100%)",
                          boxShadow: "0 4px 14px 0 rgba(99, 102, 241, 0.4)",
                          "&:hover": {
                            background:
                              "linear-gradient(135deg, primary.light 0%, secondary.main 100%)",
                            boxShadow: "0 8px 25px 0 rgba(99, 102, 241, 0.5)",
                          },
                        }}
                      >
                        Sign Up
                      </Button>
                    </Box>
                  </Fade>
                )}
              </CardContent>
            </Card>
          </Fade>
        </Container>
      </Box>
    </ThemeProvider>
  );
};

export default Auth;
